
.home .title {
   font:var(--typography-mobile-headline1); 
}

.home p {
  margin-bottom: 20px;
  font: var(--typography-mobile-body2);
  color: var(--color-primary);

  @media (min-width: 769px) {
    font: var(--typography-desktop-body2);
  }
}

.home button {
  font: var(--typography-mobile-button);
  padding: 8px 16px;
  margin-top: 16px;
  border: 1px solid var(--color-secundary);
  cursor: pointer;
  color: #fff;
  background-color: var(--color-secundary);
  box-shadow: 3px 3px 9px var(--color-secundary--50);
  border-radius: 18px;

  @media (min-width: 768px) {
    font: var(--typography-tablet-button);
  }

  @media (min-width: 769px) {
    font: var(--typography-desktop-button);
  }
}

.home button:hover {
  background-color: var(--color-secundary--100);
}
